import React from "react";
import "./Busy.css";
import Nav from "../../components/Nav";

export default function Busy(props) {
  const [selectedDay, setSelectedDay] = React.useState(0);
  const orderedDays = [];
  for (var i = 1; i < 31; i++) {
    for (var j = 0; j < 11; j++) {
      if (parseInt(props.busyPrompt[j].fields.Day) === i) {
        orderedDays.push(props.busyPrompt[j].fields);
      }
    }
  }
  console.log(orderedDays);
  const circle_r = 15;
  const circle_x = 60;
  return (
    <div className="busy">
      <Nav />
      <div className="busy-container">
        <div className="busy-header">Alternative Schedule</div>
        <div className="busy-cols">
          <div className="busy-insp-img-container">
            <img
              className="busy-insp-img"
              src={orderedDays[selectedDay].InspURL}
            />
          </div>

          <div className="busy-prompt-container">
            {orderedDays.map((currDay, index) => (
              <div
                className={`busy-prompt-div ${
                  selectedDay === index ? "selected" : ""
                }`}
                key={index}
                onClick={() => setSelectedDay(index)}
                selected={selectedDay === index}
              >
                <div>
                  <div className="busy-prompt-header">
                    Day {currDay.Day.toString().padStart(2, "0")}:
                  </div>
                  <img className="busy-insp-svg" src={currDay.promptURL} />
                </div>

                <div className="busy-palette">
                  <ul>
                    <li style={{ color: currDay.Colour1 }}>
                      {currDay.Colour1}
                    </li>
                    <li style={{ color: currDay.Colour2 }}>
                      {currDay.Colour2}
                    </li>
                    <li style={{ color: currDay.Colour3 }}>
                      {currDay.Colour3}
                    </li>
                    <li style={{ color: currDay.Colour4 }}>
                      {currDay.Colour4}
                    </li>
                    <li style={{ color: currDay.Colour5 }}>
                      {currDay.Colour5}
                    </li>
                  </ul>
                </div>
              </div>
            ))}
          </div>
        </div>
      </div>
      <div className="busy-circles">
        <svg viewBox="30 0 60 400">
          <circle
            cy={40}
            cx={circle_x}
            r={circle_r}
            fill={orderedDays[selectedDay].Colour1}
          />
          <circle
            cy={80}
            cx={circle_x}
            r={circle_r}
            fill={orderedDays[selectedDay].Colour2}
          />
          <circle
            cy={120}
            cx={circle_x}
            r={circle_r}
            fill={orderedDays[selectedDay].Colour3}
          />
          <circle
            cy={160}
            cx={circle_x}
            r={circle_r}
            fill={orderedDays[selectedDay].Colour4}
          />
          <circle
            cy={200}
            cx={circle_x}
            r={circle_r}
            fill={orderedDays[selectedDay].Colour5}
          />
        </svg>
      </div>
    </div>
  );
}
